import React from 'react';
import styled from 'styled-components';

const EstimateTemplateBlock = styled.div`
  width: 682px;
  height: 922;

  position: relative; /*         */
  background: white;
  border-radius: 16px;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.04);

  margin: 0 auto; /*     */

  margin-top: 40px;
  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
`;

function EstimateTemplate({ children }) {
  return <EstimateTemplateBlock>{children}</EstimateTemplateBlock>;
}

export default EstimateTemplate;